import React, { useEffect, useState } from "react";
import ReactGA from 'react-ga';
import {
    useWindowWidth,
} from '@react-hook/window-size';
import LazyLoad from 'react-lazyload';
import AdsInitializer from "../AdsInitializer";
import { detected } from 'adblockdetect';

import "./styles.scss";

export default function Template({
  type
}) {
  const [isLazyLoading, setLazyLoadState] = useState(true);
  const [hasAdblocker, setHasAdblocker] = useState(false);
  useEffect(() => {
    setHasAdblocker(detected());
  }, []);

  const onlyWidth = useWindowWidth();
  const isMobile = onlyWidth <= 728;
  let adsData = null;

  if (isMobile) {
    if (type === "blog-content") {
      adsData = {
        id: "188970836", size: "300x250", id2: "188970836", isMobile: true, height: 250, width: 300
      };
    } else if (type === "blog-content-2") {
      adsData = {
        id: "854341318", size: "300x250", id2: "854341318", isMobile: true, height: 250, width: 300
      };
    } else if (type === "billboard") {
      adsData = {
        id: "617163306", size: "320x50", id2: "617163306", isMobile: true, height: 50, width: 320
      };
    } else if (type === "leaderboard") {
      adsData = {
        id: "346799754", size: "300x250", id2: "346799754", isMobile: false, height: 250, width: 300
      };
    }
  }

  if (!isMobile) {
    if (type === "leaderboard") {
      adsData = {
        id: "138821154", size: "728x90", id2: "138821154", isMobile: false, height: 90, width: 728
      };
    } else if (type === "blog-content") {
      adsData = {
        id: "188970836", size: "300x250", id2: "188970836", isMobile: false, height: 250, width: 300
      };
    } else if (type === "blog-content-2") {
      adsData = {
        id: "854341318", size: "300x250", id2: "854341318", isMobile: false, height: 250, width: 300
      };
    } else if (type === "billboard") {
      adsData = {
        id: "556006421", size: "728x90", id2: "556006421", isMobile: false, height: 90, width: 728
      };
    }
  }
  
  return null;

  const containerStyle = { height: adsData.height + 5, width: adsData.width };
  if (hasAdblocker) {
    return (
      <LazyLoad height={adsData.height} offset={1200}></LazyLoad>
    );
  }

  if (adsData) {
    const onAdLoad = () => {
      if (isLazyLoading) {
        setLazyLoadState(false);
        ReactGA.event({
          category: 'MediaAd',
          action: 'Appeared',
          label: 'MediaAdLoaded',
          value: 1,
        });
      }
    };
    const onError = () => {
      setLazyLoadState(false);
    }

    return (
      <LazyLoad height={containerStyle.height} offset={1200}>
        <div className="MediaAdsContainer" style={isLazyLoading ? containerStyle : null}>
          <AdsInitializer adsData={adsData} onAdLoad={onAdLoad} onError={onError} key={adsData.id + "-ads-init"} />
          {isLazyLoading &&
            <div className={"LazyLoadAdsContainer"}></div>
          }
          <div className={"AdsContainer"} id={adsData.id} key={adsData.id + "-ad"}></div>
        </div>
      </LazyLoad>
    );
  }

  return null;
}
