import React from "react";

import Image from "../Image";

import {AUTHOR_MAP} from "../../author/authorInfo";
import "./styles.scss";

const Author = ({ id }) => {
    const author = AUTHOR_MAP[id] || null; 
    if (!author) {
        return null;
    }

    const name = author.name || "";
    const picture = author.picture || "";
    const srcSetResolutions = [50, 75, 100, 120];
    const fullPathToPicture = picture.substring(0, picture.indexOf("?"));
    const fullPathToPictureParts = fullPathToPicture.split(".");

    const srcSet = srcSetResolutions.reduce((cum, cur) => {
        const srcSetUrl = fullPathToPictureParts[0] + "-" + cur + "." + fullPathToPictureParts[1];
        const srcSetUrlWithWidth = `${srcSetUrl} ${cur}w`;
        if (cum === "") {
            return srcSetUrlWithWidth;
        }
        return cum + `, ${srcSetUrlWithWidth}`;
    }, "");
    console.log(srcSet);

    return (
        <div className="AuthorInfo">
            <div className="AuthorBubbleImageContainer">
                <Image className="AuthorBubble" src={picture + `&srcSet=${srcSet}&sizes=25px`} alt={name} />
            </div>
            <div className="AuthorTitle">By {name}</div>
        </div>
    );
};

export default Author;
